import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

import NewsComments from '../../components/news/comments';
import NewsAddComment from '../../components/news/commentAdd';

const initialState = {
	showAddComment: false,
};

@observer
class News extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

	likeNews = async () => {
		const { newsStore } = this.props.stores;
        const { news } = this.props;
		const response = await newsStore.likeNews({ id: news.id });

        if (response.status === 200) {
			
		}
	}

	toggleAddComment = (e) => {
		const newsId = e.target.parentElement.dataset.id || e.target.dataset.id;
		const { showAddComment } = this.state;
		this.setState({
			showAddComment: !showAddComment,
		}, () => {
			if (this.inputRef[newsId]) {
				this.inputRef[newsId].focus();
			}
		});
	}

    render() {
        const { showAddComment } = this.state;
        const { news = {} } = this.props;

        const { userStore } = this.props.stores;
        const { user } = userStore;
        const { language = 'en' } = user;
        const { likes = [], comments = [] } = news;
		const hasLikedNews = news.likes && news.likes.findIndex(e => e.user === user.id) > -1;

        return (
            <>
                <div class='w-100 mb-5'>
                    <div class='row'>
                        <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                            {/* {JSON.stringify(team, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={news.images && news.images[0] ? `background-image: url("${news.images[0].s3SmallLink}");` : ''}>
                                {!(news.images && news.images[0]) && <i class='fas fa-paragraph text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                <a class='stretched-link' href={`/news/${news.id}`}>
                                    <i class={`fas fa-eye${news.active ? ' text-success' : '-slash text-danger'} float-right`} />
                                    <h5 class='mb-1' style='line-height: 1.0em;'>
                                        {mu.replaceImages(news.title, news.images, language, false)}
                                    </h5>
                                </a>
                                <div>
                                    <small class='text-muted'>
                                        {news.date && <>{util.isoDate(news.date)} - </>}{news.tag}
                                        {news.sort &&<> - <i class='fas fa-sort-amount-down' /> {news.sort}</>}
                                    </small>
                                </div>
                                <Markdown markdown={mu.replaceImages(news.ingress || '', news.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                            </div>
                        </div>

                        <div class='col-10 offset-2 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0'>
                            {likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                <i class='fas fa-thumbs-up text-primary' /> {likes.length}
                            </small>}
                            {comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                <i class='fas fa-comment text-primary' /> {comments.length}
                            </small>}
                        </div>

                        <div class='col-10 offset-2 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-1 mb-2'>
                            <div class='row'>
                                <div class='col-6 text-center'>
                                    <button type='button' class={`btn btn-sm btn-outline-${hasLikedNews ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedNews ? 'text-primary' : ''}`}
                                        onClick={this.likeNews}
                                        // disabled={hasLikedNews}
                                    >
                                        <i class={`${hasLikedNews ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> Like
                                    </button>
                                </div>
                                <div class='col-6 text-center'>
                                    <button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block'
                                        data-id={news.id}
                                        onClick={this.toggleAddComment}
                                    >
                                        <i class='fas fa-comment' /> Comment
                                    </button>
                                </div>
                            </div>
                        </div>

                        {showAddComment && <NewsAddComment
                            stores={this.props.stores}
                            news={news}
                            inputRef={c => this.inputRef[news.id] = c}
                        />}
                        <NewsComments stores={this.props.stores} news={news} col={10} offset={2} compact={true} />


                    </div>
                </div>
            </>
        );
    }
}

export default News;
