import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import News from '../../components/news';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
	isLoading: false,
};

@observer
class NewsList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadNews(props = this.props) {
		this.setState({ isLoading: true });
		const { newsStore, userStore } = props.stores;
		const { isAdmin } = userStore;
		const query = {
			tag: props.tag,
		};
		if (isAdmin) {
			query.showall = 1;
			delete query.tag;
		}
		await newsStore.load({ query });
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadNews();
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.tag !== this.props.tag) {
			this.loadNews(nextProps);
		}
	}

	render() {
		const { tag } = this.props;
		const { newsStore, userStore } = this.props.stores;
		const { newsList } = newsStore;
		const { isAdmin } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5>{tag === 'news' ? <Text id='news.heading'>Siste nytt</Text> : <Text id='news.heading-howo'>Hvordan gjør man det?</Text>}</h5>
						</div>
						{isAdmin && <a class='btn btn-sm btn-success float-right ml-2'
							href={`/news/edit/new`}
						>
							<i class='fas fa-plus' /> <Text id='news.create'>Ny</Text>
						</a>}

						{newsList && newsList.map(n => <News stores={this.props.stores} news={n} />)}
					</div>
				</div>
			</div>
		);
	}
}

export default NewsList;
